@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";
@import "./components/swiper.css";
@import "./components/wp.css";

[v-cloak] {display: none}
.size-full{
  @apply w-full;
}
html{
  @apply h-full;
}
html, body{
  @apply  min-h-full;
}
body {
  @apply flex w-full min-h-full;
}

.button {
  @apply text-black uppercase inline-flex items-center whitespace-nowrap justify-center px-5 py-2 border-2 border-gray-900 leading-6 font-bold rounded-full bg-transparent transition duration-150 ease-in-out cursor-pointer;
}
.button:focus{
  @apply outline-none shadow-outline;
}
.button:disabled{
  @apply opacity-50 cursor-not-allowed;
}
.button.green {
  @apply bg-green-500 border-green-500 text-gray-900 hover:bg-green-600 hover:border-green-600;
}
.navigation .menu-item a {
  @apply text-base font-semibold transition;
}
.navigation .current-menu-item a{
  @apply opacity-75;
}
.navigation .menu-item a:hover{
  @apply opacity-75;
}
.button.white {
  @apply border-white hover:border-white;
}
.button.solid.white {
  @apply text-gray-900 bg-white border-white hover:border-black hover:bg-black hover:text-white ;
}
.button.solid.black {
  @apply text-white border-black hover:border-black bg-black hover:bg-gray-800;
}

.mobile-menu li { padding: 0.5rem; }


[type='text'],
[type='email'],
[type='url'],
[type='password'],
[type='number'],
[type='date'],
[type='datetime-local'],
[type='month'],
[type='search'],
[type='tel'],
[type='time'],
[type='week'],
[multiple],
textarea,
select {
  @apply px-0 py-2 border-0 border-b-2 bg-transparent border-gray-200 w-full focus:border-gray-700 focus:ring-0 focus:ring-gray-200 transition-colors;
}
.boxes{
  margin-top:-9rem!important;
}
@media (max-width:600px){
  .boxes{
    margin-top:-6rem!important;
  }
}
.textarea.white,
.input.white{
  @apply placeholder-white placeholder-opacity-50 border-white;
}

[type='checkbox']{
  @apply rounded;
}
[type='checkbox'],
[type='radio'] {
  @apply border-2 border-gray-900 text-gray-900 focus:border-gray-700 focus:ring-2 focus:ring-gray-200 mr-1 -mt-1;
}

.label {
  @apply block mb-1 uppercase text-sm font-bold;
}

.prose .button {
  @apply no-underline;
}
